import React from 'react'
import Switch from 'react-switch'
import cx from 'classnames'
import './SwitchToggle.scss'

const SwitchToggle = ({ input, label, labelPosition, disabled, selectedDisableIcon }) => {
  const SwitchClass = cx({
    'switch-toggle__button': true,
    'switch-toggle__button--hidden': disabled && selectedDisableIcon
  })

  return (
    <div className='switch-toggle__container'>
      {
        labelPosition === 'left' && (
          <div className='switch-toggle__label'>
            {label}
          </div>
        )
      }
      <Switch
        {...input}
        onChange={input.onChange}
        checked={input.value}
        id={input.name}
        className={SwitchClass}
        offColor={'#c2c6c9'}
        onColor={'#4353ff'}
        uncheckedIcon={false}
        checkedIcon={false}
        height={26}
        width={55}
        disabled={disabled}
      />
      {
        disabled && selectedDisableIcon
      }
      {
        labelPosition === 'right' && (
          <div className='switch-toggle__label'>
            {label}
          </div>
        )
      }
    </div>
  )
}

SwitchToggle.defaultProps = {
  labelPosition: 'right'
}

export default SwitchToggle



