import React, { Component } from 'react'
import PropTypes from 'prop-types'
import scriptLoader from 'react-async-script-loader'
import { Element } from 'react-scroll'
import { head, join, tail, not } from 'ramda'
import Geosuggest from 'react-geosuggest'
import cx from 'classnames'
import withTranslations from 'Shared/hoc/withTranslations'
import LocationMap from 'Shared/components/LocationMap'
import { stringifyLocationCoords, parseLocationCoords, getAddressComponentStringByType } from 'Shared/helpers/Location'
import Label from '../Label/Label'

import './SearchMap.scss'

class SearchMap extends Component {
  static propTypes = {
    mapVisible: PropTypes.bool,
    suggestTypes: PropTypes.array.isRequired
  }

  static defaultProps = {
    mapVisible: true,
    suggestTypes: ['establishment'],
    selectedCountry: null
  }

  state = {
    location: {lat: 52.4126303, lng: 13.2694411}, //null,
    ready: false,
    lastSuggest: null
  }

  componentDidMount () {
    const { single, input: { value: { locationCoords, address } } } = this.props

    const newState = {
      ...(single && { address: this.props.input.value }),
      ...(this.props.isScriptLoaded && this.props.isScriptLoadSucceed && { ready: true }),
      ...(not(single) && !!locationCoords && !!address && {
        location: parseLocationCoords(locationCoords),
        address: address
      })
    }

    return this.setState(newState)
  }

  componentWillReceiveProps ({ isScriptLoaded, isScriptLoadSucceed, input }) {
    const newState = {
      ...(this.props.single && { address: input.value }),
      ...(isScriptLoaded && !this.props.isScriptLoaded && isScriptLoadSucceed && { ready: true })
    }

    return this.setState(newState)
  }

  /**
   * Handles the search event that's triggered when user either chooses the place from the select list
   * or blurs the field.
   * @param searchEvent - Event containing gmaps instance and other useful information such as placeId.
   * @return Function
   */
  onSuggestSelect = searchEvent => {
    this.setState({ lastSuggest: null })
    return searchEvent && searchEvent.description && searchEvent.hasOwnProperty('placeId')
      ? this.passLocationData(searchEvent)
      : this.resetMap()
  }

  passLocationData = ev => {
    // eslint-disable-next-line camelcase
    const { location, gmaps: { address_components, formatted_address, place_id } } = ev
    const cityValue = getAddressComponentStringByType(address_components, ['locality', 'postal_town', 'administrative_area_level_3', 'administrative_area_level_1'])
    switch (this.props.single) {
      case 'city':
        this.props.input.onChange(cityValue)
        this.props.input.onBlur(cityValue)
        break
      default:
        const data = {
          googlePlacesId: place_id,
          locationCoords: stringifyLocationCoords(location),
          city: cityValue,
          country: getAddressComponentStringByType(address_components, ['country'], 'short_name'),
          address: formatted_address
        }
        this.props.input.onChange(data)
        this.props.input.onBlur(data)
        break
    }

    return this.setState({ location })
  }

  resetMap = () => {
    this.props.input.onChange(null)
    return this.setState({ location: null })
  }

  makeBold = (label, matchedSubstrings) => {
    const { length, offset } = matchedSubstrings
    const searchedPhrase = label.substring(offset, (offset + length))
    const [start, end] = label.split(searchedPhrase)

    return <div>{start}<b>{searchedPhrase}</b>{end}</div>
  }

  renderItem = (e) => {
    const addressParts = e.label.split(',')
    const companyName = head(addressParts)
    const rest = join(',', tail(addressParts))
    return (
      <div className='suggest__item-content'>
        <div className='suggest__item__title'>{this.makeBold(companyName, e.matchedSubstrings)}</div>
        <div className='suggest__item__address'>{rest}</div>
      </div>
    )
  }

  handleActivateSuggest = (e) => {
    return this.setState({ lastSuggest: e })
  }

  handleOnBlur = () => {
    return this.geosuggestComponent.selectSuggest(this.state.lastSuggest)
  }

  render () {
    const { location } = this.state
    const { input, meta: { touched, error, submitting }, mapVisible, suggestTypes, t, label, placeholder, selectedCountry } = this.props
    const formGroupClass = cx({
      'form__group': true,
      'form__group--has-error': !submitting && touched && error
    })

    return (
      <div className={formGroupClass}>
        {input.name && <Element name={`error-position-${input.name}`} />}
        {label && <Label>{touched && error ? t(error.key, error.options) : label}</Label>}
        <div className='suggest__container'>
          { this.state.ready &&
          (<Geosuggest
            {...input}
            ref={(el) => { this.geosuggestComponent = el }}
            onChange={() => {}}
            autoActivateFirstSuggest
            value={input.value}
            initialValue={this.state.address}
            types={suggestTypes}
            country={selectedCountry}
            placeholder={placeholder}
            inputClassName={'form__input'}
            onSuggestSelect={this.onSuggestSelect}
            renderSuggestItem={this.renderItem}
            onBlur={this.handleOnBlur}
            disabled={not(this.state.ready)}
            onActivateSuggest={this.handleActivateSuggest}
          />)
          }
          {/* {this.state.ready && mapVisible && location && ( */}
            <LocationMap
              location={location}
              loadingElement={<div style={{ height: `100%` }} />}
              containerElement={<div style={{ height: `400px`, marginTop: '8px' }} />}
              mapElement={<div style={{ height: `100%` }} />}
            />
          {/* )} */}
        </div>
      </div>
    )
  }
}

export default scriptLoader(`https://maps.googleapis.com/maps/api/js?key=${process.env.REACT_APP_GOOGLE_PLACES_JAVASCRIPT_API_KEY}&libraries=places&language=en`)(withTranslations(SearchMap))



